import React from "react";
import conf from "../../conf/conf";
import {useHistory} from 'react-router-dom'
import useClick from "../../modules/hooks/useClick";

// English -> .box puple
// Japanese -> .box blue
// China -> .box pink

/**
 * status
 *   : confirm
 *   : done
 *   : pending
 *   : reserved
 *  : nonattendance
 */
const CONFIRM = " ";
const DONE = " ";
const PENDING = " ";
const RESERVED = " ";
const NON_ATTENDANCE = "";

/**
 * Item for Mobile Schedule
 * @param id
 * @param language
 * @param name
 * @param time
 * @param status
 * @param zoomPw
 * @param zoomId
 * @returns {JSX.Element}
 * @constructor
 */
export default function Item({id ,language, name, time, status, zoomPw, zoomId}) {

    const history = useHistory();

    const application = useClick(()=>{
        history.push(`schedule/${id}`)
    });

    const setStatus = (status) => {
        if (typeof status === "boolean") {
            return status;
        } else {
            switch (status) {
                case "confirm":
                    return CONFIRM;
                case "done":
                    return DONE;
                case "pending":
                    return PENDING;
                case "reserved":
                    return RESERVED;
                case "absent":
                    return NON_ATTENDANCE;
                default:
                    break;
            }
        }
    };
    const scheduleInfo = {
        class:
            typeof status === "boolean" ? status ? "box blue" : "box pink" :
                status === "done" || status === "confirm"
                    ? "box puple"
                    : status === "pending" || status === "reserved"
                    ? "box blue"
                    : "box pink",
        language:
            language === conf.language.ENGLISH ? "[]" : language === conf.language.JAPANESE ? "[]" : "[]",
        name,
        time,
        status: setStatus(status),
    };
    return (
        <>
            <div className={scheduleInfo.class}>
                <ul>
                    <li>{`${scheduleInfo.language} ${scheduleInfo.name}`}</li>
                    <li className="eng">{scheduleInfo.time.map((e) => e)} </li>
                </ul>
                {
                    status === "reserved"? <ul>
                        <li>
                            {/*  : {zoomId}*/}
                        </li>
                    </ul>:
                        <></>
                }
                {((status) => {
                    if (!typeof status === "boolean")
                        if (status !== "done" && status !== "confirm") {
                            return (
                                <div>
                                    <img src="/global/mobile/img/cancel_btn.png" alt=" "/>
                                </div>
                            );
                        }
                })(status)}
                {((status) => {
                    if (typeof status !== "boolean") {
                        return <span>{status==="reserved"? `${scheduleInfo.status}` :`${scheduleInfo.status}`}</span>
                    } else {
                        return status ? <span ref={application}></span> : <span> </span>;
                    }
                })(status)}
            </div>
            {/* <div className="box puple">
				<ul>
					<li>[]  </li>
					<li className="eng">AM 09:00 ~ PM 01:00 </li>
				</ul>
				<span> </span>
			</div>
			<div className="box blue">
				<ul>
					<li>[]  </li>
					<li className="eng">AM 09:00 ~ PM 01:00 </li>
				</ul>
				<a href="#">
					<img src="/img/cancel_btn.png" alt=" " />
				</a>
				<span> </span>
			</div>
			<div className="box pink">
				<ul>
					<li>[]  </li>
					<li className="eng">AM 09:00 ~ PM 01:00 </li>
				</ul>
				<a href="#">
					<img src="/img/cancel_btn.png" alt=" " />
				</a>
				<span> </span>
			</div>
			<div className="box pink">
				<ul>
					<li>[]  </li>
					<li className="eng">AM 09:00 ~ PM 01:00 </li>
				</ul>
				<a href="#">
					<img src="/img/cancel_btn.png" alt=" " />
				</a>
				<span></span>
			</div> */}
        </>
    );
}
